// import logo from './logo.svg';
import './App.css';
import GLTFViewer from './GLTFViewer';
// import CitiesObj from './CitiesObj';
import CityList from './CityList';
import React, { useState,useCallback } from 'react';
function App() {
  const [cityNum,setCityNum] = useState('set1.16');
  // make wrapper function to give child
  const wrapperSetParentState = useCallback(val => {
    setCityNum(val);
  }, [setCityNum]);
  const viewer_width = window.innerWidth*0.93;
  const viewer_height = window.innerHeight;
  return (
    <div style={{display:'flex'}}>
      <div width="20%">
        <CityList parentState={cityNum} parentStateSetter={wrapperSetParentState} />
      </div>
      <div width={viewer_width} height={viewer_height}>
        {/* <CitiesObj/> */}
        <GLTFViewer value={cityNum} />
      </div>
    </div>
  );
}

export default App;
